import {Component, OnInit} from "@angular/core";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {ActivatedRoute, Router} from "@angular/router";
import {AppAssets} from "app/shared/data/app-assets";
import {loginMV} from "app/shared/models/auth/auth";
import {AuthService} from "app/shared/services/auth/auth.service";
import {Storage} from "app/shared/services/local-storage";
import {NgxSpinnerService} from "ngx-spinner";

@Component({
  selector: "app-login-page",
  templateUrl: "./login-page.component.html",
  styleUrls: ["./login-page.component.scss"],
})
export class LoginPageComponent implements OnInit {
  submitted = false;
  isFailed = false;
  errMsg = "";
  returnUrl?: any;

  loginForm: FormGroup;

  constructor(
    private router: Router,
    private fb: FormBuilder,
    private authService: AuthService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    public appAssets: AppAssets
  ) {
  }

  ngOnInit(): void {
    this.returnUrl = this.route.snapshot.queryParams["returnUrl"];
    this.loginForm = this.fb.group({
      account_email: ["", [Validators.required, Validators.email]],
      account_password: ["", [Validators.required]],
    });
  }

  get FormCtrls() {
    return this.loginForm.controls;
  }

  // login
  login() {
    this.submitted = true;
    if (this.loginForm.valid) {
      this.spinner.show();
      this.authService
        .login<loginMV, any>(this.loginForm.value)
        .subscribe((res) => {
          this.spinner.hide();
          this.submitted = false;
          this.isFailed = false;
          if (res.returnValue == 0) {
            //TOKEN
            Storage.setToken(res.authToken.token);

            // IsUserLoggedIn
            Storage.setIsUserLoggedIn(true);

            // user type id
            Storage.setUserTypeId(res.user_type_id);

            // year Id
            Storage.setYearId(res.yearId);

            // userId
            Storage.setUserId(res.user_id);

            // Educomp list
            Storage.setEduCompList(res.authToken.EduCompList);

            // current EducompId
            this.authService.setEduCompId(res.authToken.EduCompList[0] ?? null);

            if (res.user_type_id == 2) {
              // current EducompId
              this.authService.setEduCompId(res.eduCompId ?? null);
              // this.router.navigateByUrl(this.returnUrl || "student");
              this.router.navigateByUrl("/home");
            } else if (res.user_type_id == 6)
              this.router.navigateByUrl("/support/search" || "home");
            else this.router.navigateByUrl("/home");
          }

          // if login Error
          else if (res.returnValue == -1) {
            this.isFailed = true;
            this.errMsg = "labels.loginErro";
          } else {
            this.isFailed = true;
            this.errMsg = res.returnString;
          }

          this.authService.isUser$.next(true)
        });
    }
  }
}
